<template>
  <div class="connectwifi">
       <head-top title="AConnecting To Router" go-back="true"></head-top>
       <div class="circle_progress">
         <div class="progress_bar">
          <svg xmlns="http://www.w3.org/200/svg" height="160px" width="160px">
            <circle class="circle" cx="80px" cy="80px" :r="radius + 'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round" />
          </svg>
          <svg xmlns="http://www.w3.org/200/svg" height="160px" width="160px" class="circle_top">
            <circle class="circle" cx="80px" cy="80px" :r="radius + 'px'" fill="none" stroke="#01a29c" stroke-width="5px" :stroke-dasharray="strokeDasharray" :stroke-dashoffset="strokeDashoffset" stroke-linecap="round" />
          </svg>
          <div class="icon" :style="{transform: 'rotate('+ percentage/100*360 +'deg)'}">
            <img src="../../../assets/addevice/click@3x.png">
          </div>
          <div class="number" :style="{transform: 'rotate('+ percentage/100*360 +'deg)'}">
            <span :style="{transform: 'rotate('- 4*percentage/100*360 +'deg)'}">{{percentage}}%</span>
          </div>
         </div>
       </div>
       <div class="prompt">
           <p>Device is connecting to router</p>
           <p>Try to make your routers, mobile phones and devices close to each other</p>
           <p ref="eltime">{{timeText}}</p>
       </div>
       <!-- 退出配网 -->
       <!-- <messageBox setHeight="180" setType="confirm" v-show="messageBoxType==='exitConnect'">
        <div slot="confirmColumn" class="common_alert unconnectalert">
          <div class="alert_icon">
            <span></span>
          </div>
          <div class="alert_describe">{{$t('homePage.distributionNetwork')}}</div>
         <div class="alert_operation">
            <div class="cancle">
              <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
            </div>
            <div class="determine">
              <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
            </div>
          </div>
        </div>
      </messageBox> -->
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
// import { stopConnectionNetworkRequest } from '../../service/method.js'
// import { connectionNetworkResponse } from '../../service/getData.js'
import headTop from '../../../components/common/head'
import messageBox from '../../../components/common/messageBox'
export default {
  data () {
    return {
      time: '03:00',
      timeText: `3 minutes 0 .seconds`,
      radius: 75, // svg进度条半径
      percentage: 0, // 百分比
      timer: null, // 定时器
      barTimer: null // 定时器
    }
  },
  created () {},

  mounted () {
    // 设备连接路由器网络返回
    // connectionNetworkResponse(this.NAVIGATOR, this)

    if (this.$root.ISDEBUG) {
      setTimeout(() => {
        // window.connectionNetworkResponse(true)
        // window.switchNetworkResponse('MOBILE', 1)
      }, 2000)
    }
    setTimeout(() => {
      this.$router.push({path: '/Me/choose'})
    }, 3000)
    this.timedown()
    this.barTimedown()
  },
  destroyed () {
    clearInterval(this.timer) // 清除定时器
    clearInterval(this.barTimer) // 清除定时器
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBox', 'messageBoxType', 'transitiondre'
    ]),

    strokeDasharray () {
      return (Math.floor(2 * Math.PI * this.radius) + ',' + Math.floor(2 * Math.PI * this.radius))
    },

    strokeDashoffset () {
      return Math.floor(2 * Math.PI * this.radius) - Math.floor(2 * Math.PI * this.radius) / 100 * this.percentage
    }
  },

  methods: {
    ...mapMutations(['MESSAGEBOX_SWITCH']),
    timedown () {
      let m = parseInt(this.time.split(':')[0])
      let s = parseInt(this.time.split(':')[1])
      this.timer = setInterval(() => {
        if (--s < 0) {
          s = 59
          m--
        }
        // this.time = (m < 10 ? `0${m}` : m) + ':' + (s < 10 ? `0${s}` : s)
        this.timeText = `${m} ${'minutes'} ${s} ${'seconds'}`
        if (m === 0 && s === 0) {
          clearInterval(this.timer)
          this.$router.push({path: '/Me/configurefailed'})
          // 停止配置网络
          // stopConnectionNetworkRequest(this.NAVIGATOR, this)
        }
      }, 1000)
    },

    barTimedown () {
      this.barTimer = setInterval(() => {
        if (++this.percentage === 100) {
          clearInterval(this.barTimer) // 清除定时器
        }
      }, 1000 * 180 / 100)
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: 'exitConnect'})
          break
        case 1:
          // 停止配置网络
          // stopConnectionNetworkRequest(this.NAVIGATOR, this)

          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.$store.state.transitiondre = true
          this.$router.go(-1)
          break
        default:
          break
      }
    },
    navjump () {
      this.MESSAGEBOX_SWITCH({type: 'exitConnect'})
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../../styles/mixin.less';
  .connectwifi{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
    .circle_progress{
      .wh(100%, 450px);
      padding-top: 80px;
      .progress_bar{
        .wh(320px, 320px);
        margin: 0 auto;
        position: relative;
        .circle_top{
          position: absolute;
          top: 0;
          left: 0;
          transform-origin: center;
          transform: rotate(-90deg);
          .circle{
            transition: all 1.8s ease-in-out;
          }
        }
        .icon, .number{
          width: 152px; /* no */
          height: 152px; /* no */
          border-radius: 50%;
          position: absolute;
          top: 4px; /* no */
          left: 4px; /* no */
          transform-origin: center;
          transition: all 1.8s ease-in;
          // border: 1px solid red;
          img{
            height: 20px; /* no */
            position: absolute;
            top: -8px; /* no */
            left: 64px; /* no */
          }
        }
        .number{
          .wh(180px,180px);
          top:50%;
          margin-top: -90px;
          left: 50%;
          margin-left: -90px;
          span{
            display: block;
            margin: 0 auto;
            color: @themeColor;
            font-size: 46px;
            font-weight: 700;
          }
        }
      }
    }
   .prompt{
        width:100%;
        text-align: center;
        font-size: 28px;
        p:nth-child(1){
            color: #585858;
            margin-top: 16px;
            font-size: 34px;
        }
        p:nth-child(2){
            color: #b7b7b7;
            margin: 0 auto;
            margin-top:28px;
            line-height: 71px;
            width:520px;
        }
         p:nth-child(3){
            color: #585858;
            margin-top:28px;
            line-height: 71px;
            font-size: 28px;
        }
    }
  }
</style>



